.gpt3__possibility {
  display: flex;
  flex-direction: row;
}

.gpt3__possibility-image {
  /* implementing flex in a child container will hide the image until the entire flex box is implemented */
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin-right: 2rem;
}

.gpt3__possibility-image img {
  width: 100%;
  height: 100%;
}

.gpt3__possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

/* styling the top h4 */
.gpt3__possibility-content h4 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71e5ff;
}

/* styling the bottom h4 */
.gpt3__possibility-content h4:last-child {
  color: #ff8a71;
}

/* styling for the h1 */
.gpt3__possibility-content h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;

  margin: 1rem 0;
}

/* styling for the p tag */
.gpt3__possibility-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-size: 16px;
  line-height: 30px;

  color: #81afdd;
  margin-bottom: 2rem;
}

/* mobile responsiveness */
@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;
  }

  .gpt3__possibility-image {
    margin: 1rem 0;
  }

  .gpt3__possibility-content {
    margin-top: 2rem;
  }
}
